import React from 'react';

const NotFound = () => {
  return (
    <div>
      <h1 className="display-4">
        404 Page Not Found
      </h1>
      <p className="lead">
        Sorry, The requested page does not exist
      </p>
    </div>
  );
};

export default NotFound;


